.colortxt {
  width: 180px;
  font-weight: bold;
  color: #7f8c8d;
  position: relative;
  left: 395px;
  top: 30px;
}

.yinput {
  display: none;
}

.ybutton {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 50px;
  margin: 10px;
  cursor: pointer;
  left: 300px;
}

.ybutton span {
  display: block;
  position: absolute;
  width: 50px;
  height: 50px;
  padding: 0;
  top: 180%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  border-radius: 100%;
  background: #eeeeee;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  transition: ease 0.3s;
}

.ybutton span:hover {
  padding: 10px;
}

.yamber .ybutton span {
  background: #ffc107;
}

.ylime .ybutton span {
  background: #8bc34a;
}

.yteal .ybutton span {
  background: #009688;
}

.yblue .ybutton span {
  background: #2196f3;
}

.yindigo .ybutton span {
  background: #3f51b5;
}

.amber {
  color: #ffc107;
}
.lime {
  color: #8bc34a;
}
.teal {
  color: #009688;
}
.blue {
  color: #2196f3;
}
.indigo {
  color: #3f51b5;
}

