.ainfo {
    position: relative;
    top: -20px;
    padding-left: 15px;
    font-size: 20px;
}
.acontainer {
    position: relative;
    width: auto;
    height: 300px;
    margin: 100px 100px;

    .abox {
        position: relative;
        width: calc(400px - 30px);
        height: calc(300px - 30px);
        background: #000;
        float: left;
        margin: 15px;
        box-sizing: border-box;
        overflow: hidden;
        border-radius: 10px;
        a {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2
        }
        .aicon {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #f00;
            transition: 0.5s;
            z-index: 1;
        }
        &:hover .aicon {
            top: 20px;
            left: calc(50% - 40px);
            width: 80px;
            height: 80px;
            border-radius: 50%;
        }
        .aicon .fa {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 80px;
            transition: 0.5s;
            color: #fff;
        }
        &:hover .aicon .fa {
            font-size: 40px;
        }
        .acontent {
            position: absolute;
            top: 100%;
            height: calc(100% - 100px);
            text-align: center;
            padding:20px;
            box-sizing: border-box;
            transition: 0.5s;
            opacity: 0;
            h3 {
                margin: 0 0 10px;
                padding: 0;
                color: #fff;
                font-size: 24px;
            }
            p {
                margin: 0;
                padding: 0;
                color: #fff;
            }
        }
        &:hover .acontent {
            top: 100px;
            opacity: 1;
        }
        &:nth-child(1) .aicon {
            background: #d03852;
        }
        &:nth-child(1) {
            background: #f54967;
        }
        &:nth-child(2) .aicon {
            background: #23798e;
        }
        &:nth-child(2) {
            background: #328fa5;
        }
    }
}